.field-select-wrapper {
	position: relative;

	width: 100%;

	&--bordered {
		max-width: 330px;
		margin: 0 auto;
		margin-bottom: 20px;

		border: 2px solid #252554;
		box-shadow: 0 5px 10px rgba(37, 37, 84, 0.2);
		border-radius: 25px;
	}

	&::before {
		position: absolute;
		top: 50%;
		right: 10px;
		transform: translateY(-50%);
		z-index: -1;

		content: '';
		display: block;
		width: 30px;
		height: 30px;

		background-image: url("../images/icons/select-arrow.svg");
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
	}
}
