.about-text-list {
	margin: 0;
	padding: 0;
	list-style: none;

	&__item {
		position: relative;

		display: block;
		margin-bottom: 8px;
		padding-left: 40px;

		font-weight: 500;

		@media (min-width: $screen-md) {
			padding-left: 120px;
		}

		&::before {
			position: absolute;
			top: 4px;
			left: 23px;

			content: '';
			display: block;
			width: 8px;
			height: 8px;

			background-color: $color-secondary;
			border-radius: 50%;

			@media (min-width: $screen-md) {
				left: 103px;
			}
		}
	}
}
