.call-buttons {
	position: relative;
	z-index: 1;

	display: flex;
	justify-content: space-between;

	margin-top: auto;

	background-color: $color-main;

	&::before {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);

		content: '';
		display: block;
		width: 1px;
		height: 40px;
		background-color: rgba(255, 255, 255, 0.1);

		@media (min-width: $screen-md) {
			display: none;
		}
	}

	&__scroll {
		position: fixed;
		bottom: 38px;
		left: 50%;
		transform: translateX(-50%);
		z-index: 1;

		display: block;

		margin: 0;
		padding: 0;

		border: none;
		background-color: transparent;
		cursor: pointer;

		@media (min-width: $screen-md) {
			bottom: -21px;
		}
	}

	&__scroll-icon {
		display: block;

		width: 102px;

		object-fit: cover;
	}

	&__item {
		position: relative;

		display: block;
		width: 100%;

		margin: 0;
		padding: 22px 16px;
		padding-left: 30px;

		font: inherit;
		font-size: $font-size-s;
		line-height: 1.25em;
		font-weight: 500;
		color: #ffffff;
		text-align: center;

		border: none;
		background-color: transparent;
		cursor: pointer;

		@media (min-width: $screen-md) {
			display: none;
		}

		&::before {
			position: absolute;
			top: 50%;
			left: 10px;
			transform: translateY(-50%);

			content: '';
			display: block;
			width: 30px;
			height: 30px;
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
		}

		&--call {
			&::before {
				left: 30px;
				background-image: url("../images/icons/phone.png");
			}
		}

		&--call-back::before {
			background-image: url("../images/icons/phone-back.png");
		}
	}
}
