.page-header {
	position: relative;

	display: flex;
	align-items: flex-start;
	justify-content: space-between;

	max-width: 900px;
	width: 100%;
	margin: 0 auto;
	padding: 0 30px;

	@media (min-width: $screen-md) {
		align-items: flex-end;
		justify-content: flex-start;

		max-width: 1160px;
	}

	@media (min-width: $screen-md + 200) {
		max-width: 1240px;
		padding: 0;
	}

	@media (min-width: $screen-lg) {
		max-width: 1620px;
	}

	&__button {
		display: block;

		margin: 0;
		margin-right: auto;
		padding: 0;

		border: none;
		background-color: transparent;
		cursor: pointer;

		@media (min-width: $screen-md) {
			display: none;
		}
	}

	&__button-icon {
		display: block;

		width: 30px;
		height: 30px;

		object-fit: contain;
	}
}
