.first-frame {
	position: relative;
	z-index: 1;

	display: flex;

	min-height: 100vh;
	min-height: calc(100vh - var(--browser-address-bar, 0px));

	background-color: #F3F5DD;

	background-image: url("../images/index/video-placeholder-mobile.jpg");
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;

	@media (min-width: $screen-md) {
		overflow: hidden;

		background-image: none;
	}

	&__page-header {
		margin-bottom: 32px;

		@media (min-width: $screen-md) {
			margin-bottom: 220px;
		}

		@media (min-width: $screen-lg) {
			margin-bottom: 416px;
		}
	}

	&--auto-height {
		min-height: 810px;
		height: auto;
	}
}
