.info-slider {
	display: block;

	margin: 0 -20px;
	padding: 0;
	list-style: none;

	@media (min-width: $screen-md) {
		margin: 0;
	}

	&__picture {
		display: block;

		min-height: 162px;
	}

	&__item {
		display: flex;
		flex-direction: column;

		max-width: 240px;
		width: 100%;
		height: auto;
		padding: 0 20px;
		padding-bottom: 8px;

		text-align: center;

		@media (min-width: $screen-md) {
			padding-left: 0;
			padding-right: 0;
			padding-bottom: 0;
			margin-right: 60px;
		}

		&:last-child {
			@media (min-width: $screen-md) {
				margin-right: 0;
			}
		}
	}

	&__img {
		display: block;
		margin: 0 auto;

		object-fit: contain;

		&--info1 {
			width: 140px;
		}

		&--info2 {
			width: 165px;
		}

		&--info3 {
			width: 147px;
		}

		&--info4 {
			width: 177px;
		}
	}

	&__title {
		margin: 0;
		margin-bottom: 10px;

		font-size: $font-size-m + 6;
		line-height: 1.25em;
		font-weight: 700;
		color: $color-secondary;
	}

	&__text {
		max-width: 180px;
		width: 100%;
		margin: 0 auto;

		font-weight: 500;
	}
}
