.about-links {
	@media (min-width: $screen-md) {
		display: flex;
		align-items: center;
	}

	&__button,
	&__link {
		display: block;

		max-width: 330px;
		width: 100%;
		margin: 0 auto;
		padding: 16px 10px;

		@media (min-width: $screen-md) {
			max-width: 210px;
			margin: 0;
		}
	}

	&__button {
		margin-bottom: 20px;

		font: inherit;
		font-weight: 700;
		line-height: 1.3em;
		color: #ffffff;
		text-align: center;

		border: none;
		background-color: $color-main;
		border-radius: 25px;
		box-shadow: 0 10px 30px 0 rgba(37, 37, 84, 0.5);
		cursor: pointer;
		outline: none;

		transition: all 0.3s ease-in-out;

		&:hover,
		&:focus {
			background-color: $color-secondary;
			box-shadow: 0 10px 30px 0 rgba(233, 79, 112, 0.5);
		}

		&:active {
			background-color: rgba(233, 79, 112, 0.4);
		}

		@media (min-width: $screen-md) {
			margin: 0;
			margin-right: 20px;
		}
	}

	&__link {
		font-size: $font-size-s;
		line-height: 1.5em;
		font-weight: 500;
		color: $color-secondary;
		text-transform: uppercase;
		text-align: center;

		transition: all 0.3s ease-in-out;

		&:hover {
			color: $color-main;
		}
	}
}
