.form-fields-wrapper {
	position: relative;

	display: flex;

	border-bottom: 1px solid rgba(37, 37, 84, 0.1);

	@media (min-width: $screen-md) {
		flex-shrink: 0;

		max-width: 320px;
		width: 100%;

		border-bottom: none;
		border-right: 1px solid rgba(37, 37, 84, 0.1);
	}

	&::before {
		position: absolute;
		top: -2px;
		left: 50%;
		transform: translateX(-50%);

		content: '';
		display: block;
		width: 10px;
		height: 75px;

		background-image: url("../images/icons/form-line.svg");
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
	}
}
