.call {
	display: none;

	@media (min-width: $screen-md) {
		z-index: 1;

		display: flex;

		margin-left: auto;

		&__img {
			display: block;

			width: 60px;
			margin-right: 10px;

			object-fit: contain;
		}

		&__number {
			display: block;
			margin-bottom: 10px;

			font-size: $font-size-s;
			font-weight: 600;
			color: $color-main;

			transition: all 0.3s ease-in-out;

			&:hover {
				color: $color-secondary;
			}
		}

		&__number-large {
			display: block;

			font-size: $font-size-xl - 4;
			line-height: 1.16em;
		}

		&__text {
			display: block;
			margin: 0;
			padding: 0;

			font: inherit;
			font-size: $font-size-xs;
			line-height: 1.2em;
			font-weight: 600;
			color: $color-secondary;
			text-decoration: underline;
			text-decoration-style: dashed;

			border: none;
			background-color: transparent;
			cursor: pointer;
			transition: all 0.3s ease-in-out;

			&:hover {
				color: $color-main;
				text-decoration: underline;
				text-decoration-style: dashed;
			}
		}
	}
}
