.button {
	display: block;
	width: 100%;
	padding: 16px 0;

	font: inherit;
	font-size: $font-size;
	line-height: 1.215em;
	font-weight: 700;
	color: #ffffff;
	text-align: center;
	word-break: break-word;
	text-transform: uppercase;

	border: none;
	background-color: $color-main;
	border-radius: 25px;
	cursor: pointer;
	outline: none;
	transition: all 0.3s ease-in-out;
}
