.menu-list {
	margin: 0;
	margin-bottom: 32px;
	padding: 0;
	list-style: none;

	border-bottom: 1px solid rgba(37, 37, 84, 0.1);
	border-bottom: none;

	@media (min-width: $screen-md) {
		display: flex;

		margin-bottom: 0;

		border: none;
	}

	&__item {
		margin-bottom: 16px;

		@media (min-width: $screen-md) {
			margin-bottom: 0;
			margin-right: 24px;
		}

		&--index {
			@media (min-width: $screen-md) {
				display: none;
			}
		}

		&:last-child {
			margin-bottom: 32px;

			@media (min-width: $screen-md) {
				margin-bottom: 0;
				margin-right: 0;
			}
		}
	}

	&__link {
		display: block;
		padding: 8px 0;
		padding-left: 30px;

		font-size: $font-size-xs;
		line-height: 2em;
		font-weight: 700;
		color: $color-main;
		letter-spacing: 0.1em;
		text-transform: uppercase;

		transition: all 0.3s ease-in-out;

		&:hover {
			color: $color-secondary;
		}

		@media (min-width: $screen-md) {
			padding: 8px;
			padding-bottom: 0;
		}
	}
}
