.body-overflow {
	height: 100vh;
	overflow: hidden;
}

.help-block {
	margin: 8px 0;

	font-size: $font-size - 1;
	font-weight: 600;
	text-align: center;
}

.form-group {
	margin: 0;
}

.field-select-wrapper {
	.form-group {
		height: 70px;
	}
}

.field-connection_type_send,
.field-connection_type_delivery {
	.field-select-wrapper {
		margin-bottom: 12px;

		+.help-block {
			margin-top: 0;
			margin-bottom: 16px;
		}
	}
}

.field-sendform-connection,
.field-deliveryform-connection,
.field-calls-number,
.field-subscription-email {
	.field-text {
		margin-bottom: 12px;

		+.help-block {
			margin-top: 0;
			margin-bottom: 16px;
		}
	}
}

.field-deliveryform-recaptcha,
.field-sendform-recaptcha {
	margin-bottom: 24px;

	.help-block {
		text-align: center;
	}
}

.g-recaptcha {
	display: flex;
	justify-content: center;
}

.page-header {
	border-bottom: 0;
}

.container {
	width: auto;

	padding-left: 0;
	padding-right: 0;
}

.field-dateTo-one,
.field-dateToSend-one {
	@media (min-width: $screen-md) {
		flex-shrink: 0;

		max-width: 180px;
		width: 100%;
	}
}

.field-calls-number {
	@media (min-width: $screen-md + 200) {
		flex-shrink: 0;

		max-width: 340px;
		width: 100%;
		margin-bottom: 0;
		margin-right: 10px;
	}
}
