.modal {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 10;

	display: none;
	width: 100%;
	height: 100%;

	background: rgba(233, 79, 112, 0.5);
	backdrop-filter: blur(27.1828px);

	&--alert {
		position: fixed;

		background: none;
		backdrop-filter: none;
	}

	&--fixed {
		position: fixed;
	}
}
