.about-page-slider {
	display: block;

	margin: 0 -20px;
	padding: 0;
	list-style: none;

	@media (min-width: 980px) {
		margin: 0;
	}

	&__item {
		display: block;

		padding-left: 20px;

		@media (min-width: 980px) {
			padding-left: 0;
			margin-right: 30px;
		}

		&:last-child {
			padding-right: 20px;
			@media (min-width: 980px) {
				padding-right: 0;
				margin-right: 0;
			}
		}

		&--n1 {
		}

		&--n2 {
		}

		&--n3 {
		}

		&--n4 {
		}

		&--n5 {
		}

		&--n6 {
		}

		&--n7 {
		}

		&--n8 {
		}

		&--n9 {
		}

		&--n10 {
		}
	}

	&__content {
		display: flex;
		align-items: center;
		justify-content: flex-end;

		width: 140px;
		height: 140px;
		padding: 20px;

		font-size: $font-size-s;
		line-height: 1.2em;
		font-weight: 700;
		text-align: center;

		background: rgba(0, 0, 0, 0.05);
		border-radius: 20px;

		&--column {
			flex-direction: column;

			padding: 20px 10px;
		}
	}

	&__icon {
		display: block;

		margin-top: auto;
	}

	&__text {
		margin-top: auto;
	}
}
