.footer-links {
	display: flex;
	align-items: center;
	justify-content: space-between;

	margin-bottom: 24px;

	@media (min-width: $screen-md) {
		justify-content: flex-start;
		margin-bottom: 0;
	}

	&__logo {
		display: block;

		@media (min-width: $screen-md) {
			margin-right: 30px;
		}
	}

	&__img {
		display: block;

		width: 130px;

		object-fit: contain;
	}

	&__offer {
		font-size: $font-size-s;
		line-height: 1.25em;
		font-weight: 500;
		color: $color-secondary;

		transition: color 0.3s ease-in-out;

		&:hover {
			color: $color-main;
		}
	}
}
