.intro-slider {
	margin: 0 -20px;
	padding: 0;
	list-style: none;

	@media (min-width: 700px) {
		margin: 0;
	}

	&__item {
		padding-left: 20px;

		@media (min-width: 700px) {
			padding: 0;
			margin-right: 20px;
		}

		@media (min-width: $screen-md + 200) {
			margin: 0;
			list-style: none;

			&--slide1 {
				grid-area: slide1;
			}

			&--slide2 {
				grid-area: slide2;
			}

			&--slide3 {
				grid-area: slide3;
			}
		}

		&:last-child {
			margin-right: 0;
			padding-right: 20px;

			@media (min-width: 700px) {
				padding-right: 0;
			}
		}
	}

	&__link {
		display: flex;
		align-items: flex-end;
		flex-wrap: wrap;

		width: 180px;
		height: 180px;
		padding: 20px;
		padding-bottom: 40px;

		background: rgba(0, 0, 0, 0.05);
		border-radius: 20px;

		&:last-child {
			margin-right: 0;
		}
	}

	&__icon {
		display: block;

		object-fit: contain;

		&--bill {
			width: 54px;
		}

		&--officer {
			width: 27px;
		}

		&--shield {
			width: 49px;
		}
	}

	&__text {
		display: block;

		font-size: $font-size-s;
		line-height: 1.2em;
		font-weight: 500;
		color: #3792DF;
		text-transform: uppercase;
	}
}
