.container {
	&--first-frame {
		display: flex;
		flex-direction: column;

		max-width: 1760px;
		width: 100%;
		margin: 0 auto;
		padding-top: 24px;
		padding-bottom: 0;
	}

	&--section2 {
		@media (min-width: $screen-md + 200) {
			position: relative;

			padding-top: 72px;

			clip-path: polygon(0 0,100% 0%,100% 86%,0 100%);

			&::before,
			&::after {
				position: absolute;
				z-index: 1;

				content: '';
				display: block;

				background-repeat: no-repeat;
				background-position: center;
				background-size: cover;
			}

			&::before {
				top: -16px;
				left: 15%;

				width: 194px;
				height: 194px;

				background-image: url("../images/index/box3.png");
			}

			&::after {
				top: 75px;
				left: 65%;

				width: 433px;
				height: 891px;

				background-image: url("../images/index/info2-photo@1x.png");

				@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
					background-image: url("../images/index/info2-photo@2x.png");
				}
			}
		}
	}

	&--section3 {
		@media (min-width: $screen-md + 200) {
			position: relative;

			&::before,
			&::after {
				position: absolute;
				z-index: 1;

				content: '';
				display: block;

				background-repeat: no-repeat;
				background-position: center;
				background-size: cover;
			}

			&::before {
				top: -250px;
				right: 400px;

				width: 273px;
				height: 280px;

				background-image: url("../images/index/box5.png");
				background-size: contain;
			}

			&::after {
				top: -70px;
				right: 65%;

				width: 345px;
				height: 798px;

				background-image: url("../images/index/info3-photo@1x.png");

				@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
					background-image: url("../images/index/info3-photo@2x.png");
				}
			}
		}
	}

	&--section4 {
		@media (min-width: $screen-md + 200) {
			position: relative;
			z-index: 1;
		}
	}

	&--footer {
		@media (min-width: $screen-md) {
			display: flex;
			align-items: center;
			justify-content: space-between;

			width: 100%;
		}
	}
}
