.main-nav {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 10;

	display: none;

	width: 100%;
	height: 100%;

	@media (min-width: $screen-md) {
		position: static;

		display: block;

		width: auto;
		height: auto;
	}

	&.active {
		display: block;
	}

	&__inner-wrapper {
		max-width: 220px;
		width: 100%;
		height: 100%;
		padding: 40px 0;

		background-color: #fff;
		box-shadow: 20px 0 60px rgba(23, 23, 58, 0.2);

		@media (min-width: $screen-md) {
			max-width: none;
			height: auto;
			padding: 0;

			background-color: transparent;
			box-shadow: none;
		}
	}

	&__button {
		display: block;

		margin: 0;
		margin-bottom: 40px;
		margin-left: 30px;
		padding: 0;

		font-size: 0;

		border: none;
		background-color: transparent;
		cursor: pointer;

		@media (min-width: $screen-md) {
			display: none;
		}
	}

	&__link {
		display: block;
		display: none;
		margin-left: 30px;
		padding: 8px 0;

		font-size: $font-size-xs;
		line-height: 2em;
		font-weight: 700;
		color: $color-main;
		text-transform: uppercase;

		@media (min-width: $screen-md) {
			display: none;
		}
	}
}
