.modal-form {
	position: relative;
	top: 0;
	left: 50%;
	transform: translateX(-50%);

	max-width: 600px;
	width: 100%;
	padding: 20px 0;

	background-color: #ffffff;
	box-shadow: 0 40px 70px rgba(0, 0, 0, 0.2);
	border-radius: 20px;

	@media (min-width: $screen-md) {
		top: 76px;
	}

	@media (min-width: $screen-lg) {
		top: 50%;
		transform: translate(-50%, -50%);
	}

	&--centered {
		top: 50%;
		transform: translate(-50%, -50%);
	}

	&::before,
	&::after {
		@media (min-width: $screen-md + 200) {
			position: absolute;

			content: '';
			display: block;
			width: 226px;
			height: 231px;

			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
		}
	}

	&::before {
		top: -70px;
		left: -200px;

		background-image: url("../images/index/box6.png");
	}

	&::after {
		right: -190px;
		top: 360px;

		width: 235px;
		height: 186px;

		background-image: url("../images/index/box7.png");
	}

	&--call {
		top: 50%;
		transform: translate(-50%, -50%);

		@media (min-width: $screen-md) {
			&::before {
				top: -150px;
				left: -180px;
			}

			&::after {
				right: -150px;
				top: 120px;
				z-index: -1;
			}
		}
	}

	&--send,
	&--delivery {
		@media (min-width: $screen-md) {
			top: 80px;
			transform: translateX(-50%);
		}

		@media (min-width: $screen-lg) {
			top: 50%;
			transform: translate(-50%, -50%);
		}
	}

	&__close {
		position: absolute;
		top: 10px;
		right: 10px;

		display: block;
		padding: 0;

		border: none;
		background-color: transparent;
		cursor: pointer;
	}

	&__close-icon {
		display: block;

		width: 30px;

		object-fit: contain;
	}

	&__title {
		margin: 0;
		margin-bottom: 8px;
		padding: 0 20px;
		padding-right: 40px;

		font-size: $font-size-m + 2;
		line-height: 1.5em;
		font-weight: 700;

		&--centered {
			font-size: $font-size-m;

			text-align: center;
		}
	}

	&__text {
		margin: 0;
		margin-bottom: 24px;
		padding: 0 20px;

		font-weight: 500;
	}

	&__button {
		max-width: 330px;
		margin: 0 auto;

		line-height: 1.3em;
		text-transform: none;

		box-shadow: 0 10px 30px 0 rgba(37, 37, 84, 0.5);

		transition: all 0.3s ease-in-out;

		&:hover,
		&:focus {
			background-color: $color-secondary;
			box-shadow: 0 10px 30px 0 rgba(233, 79, 112, 0.5);
		}

		&:active {
			background-color: rgba(233, 79, 112, 0.4);
		}
	}

	&__agreement {
		margin: 0;
		padding: 0 20px;

		font-size: $font-size-s;
		line-height: 1.2em;
		font-weight: 500;
		text-align: center;

		opacity: 0.4;
	}

	&__agreement-link {
		color: $color-secondary;
	}

	&__flex-wrapper {
		@media (min-width: $screen-md + 200) {
			display: flex;
			align-items: flex-start;

			max-width: 540px;
			width: 100%;
			margin: 0 auto;

			.field-text {
				flex-shrink: 0;

				max-width: 340px;
				width: 100%;
				margin-bottom: 0;
				margin-right: 10px;
			}
		}
	}
}
