.about-how-works {
	margin-bottom: 72px;
	padding: 0 20px;

	@media (min-width: $screen-md + 200) {
		flex-shrink: 0;

		height: fit-content;
		margin-bottom: 0;
		margin-right: auto;
		padding: 0;
	}

	&__button {
		display: flex;
		align-items: center;

		max-width: 185px;
		width: 100%;
		margin: 0;
		padding: 0;

		font: inherit;
		font-weight: 700;
		color: $color-main;
		text-align: left;

		background-color: transparent;
		outline: none;
		border: none;
		cursor: pointer;
	}

	&__icon {
		display: block;

		width: 68px;
		margin-right: 10px;

		object-fit: contain;
	}
}
