.yt-video {
	display: flex;
	align-items: center;

	height: 100%;
	padding: 0 20px;

	background-color: #fff;
	box-shadow: 0 40px 70px rgba(0, 0, 0, 0.2);
	border-radius: 20px;

	@media (min-width: $screen-md + 200) {
		position: relative;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);

		max-width: 780px;
		width: 100%;
		height: 540px;
		padding: 20px;
		padding-top: 48px;
	}

	&__frame {
		display: block;

		max-width: 330px;
		width: 100%;
		height: 250px;
		margin: 0 auto;

		@media (min-width: $screen-md + 200) {
			max-width: 740px;
			height: 100%;
		}
	}
}
