.about-intro-wrapper {
	margin-bottom: 40px;

	@media (min-width: $screen-md + 200) {
		position: relative;

		display: flex;
		justify-content: space-between;

		max-width: 1240px;
		width: 100%;
		margin: 0 auto;
		padding-top: 130px;
	}

	&::before,
	&::after {
		@media (min-width: $screen-md + 200) {
			position: absolute;

			content: '';
			display: block;

			background-repeat: no-repeat;
			background-position: center;
			background-size: contain;
		}
	}

	&::before {
		@media (min-width: $screen-md + 200) {
			bottom: -120px;
			left: 20%;

			width: 388px;
			height: 818px;

			background-image: url("../images/about/first-frame-photo@1x.png");

			@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
				background-image: url("../images/about/first-frame-photo@2x.png");
			}
		}

		@media (min-width: $screen-lg) {
			bottom: -60px;
			left: 25%;
		}
	}

	&::after {
		@media (min-width: $screen-md + 200) {
			bottom: 0;
			left: 0;

			width: 155px;
			height: 141px;

			background-image: url("../images/about/page1.png");
		}
	}

	@media (min-width: $screen-lg) {
		max-width: 1620px;
	}
}
