.prices {
	max-width: 860px;
	width: 100%;
	margin: 0 auto;
	margin-bottom: 48px;

	&__title {
		max-width: 270px;
		width: 100%;
		margin: 0;
		margin-bottom: 48px;
		padding: 0 20px;

		font-size: 46px;
		line-height: 1.3em;
		font-weight: 700;

		@media (min-width: 500px) {
			max-width: none;
		}
	}

	&__intro {
		margin: 0;
		margin-bottom: 32px;
		padding: 0 20px;

		font-size: $font-size + 2;
		line-height: 1.5em;
		font-weight: 500;
	}

	&__info {
		margin: 0;
		padding: 0 20px;

		font-size: $font-size + 2;
		line-height: 1.5em;
		font-weight: 500;
	}

	&__link {
		color: $color-secondary;

		transition: color 0.3s ease-in-out;

		&:hover {
			color: $color-main;
		}
	}

	&__price-list {
		margin-bottom: 40px;
	}
}
