.form {
	position: relative;
	z-index: 1;

	max-width: 330px;
	width: 100%;
	margin: 0 auto;

	background-color: #ffffff;
	box-shadow: 0 10px 50px rgba(0, 0, 0, 0.07);
	border-radius: 10px;

	@media (min-width: $screen-md) {
		display: flex;

		max-width: 640px;
		margin: 0;
	}

	@media (min-width: $screen-md + 200) {
		margin-left: 0;
	}

	&__button {
		padding: 25px 0;

		font-weight: 700;
		line-height: 20px;
		color: $color-secondary;
		text-transform: none;

		background-color: transparent;
		border-radius: 0;
	}
}
