.contacts-block {
	&__link {
		display: flex;
		align-items: center;
	}

	&__icon {
		display: block;

		width: 60px;
		margin-right: 10px;

		object-fit: contain;
	}

	&__title {
		display: block;

		font-size: $font-size-s;
		line-height: 1.2em;
		font-weight: 500;
		color: #AAAAAA;
	}

	&__text {
		font-size: $font-size + 2;
		line-height: 1.25em;
		font-weight: 700;
		color: $color-main;
	}
}
