.main-info {
	position: relative;
	z-index: 1;

	max-width: 900px;
	width: 100%;
	margin: 0 auto;
	padding: 0 30px;

	@media (min-width: $screen-md) {
		max-width: 450px;
		margin-left: auto;
		margin-right: 50px;
		padding: 0;
	}

	@media (min-width: $screen-lg) {
		max-width: 740px;
	}

	&__title {
		max-width: 290px;
		width: 100%;
		margin: 0 auto;
		margin-bottom: 40px;

		font-size: $font-size-xl;
		font-weight: 900;
		line-height: 1.34em;
		text-transform: uppercase;

		@media (min-width: $screen-md) {
			max-width: none;
			margin: 0;
			margin-bottom: 32px;

			font-size: 43px;
			line-height: 1.4em;
			letter-spacing: 1.72px;
		}

		@media (min-width: $screen-lg) {
			font-size: 73px;
			line-height: 80px;
		}
	}

	&__buttons {
		margin-bottom: 20px;

		@media (min-width: $screen-md) {
			display: flex;
		}
	}

	&__button {
		display: block;

		max-width: 290px;
		margin: 0 auto;

		line-height: 1.3em;

		transition: all 0.3s ease-in-out;

		box-shadow: 0 10px 30px 0 rgba(37, 37, 84, 0.50);

		@media (min-width: $screen-md) {
			max-width: 210px;
			margin: 0;
		}

		&:hover,
		&:focus {
			color: #ffffff;

			background-color: $color-secondary;
			box-shadow: 0 10px 30px 0 rgba(233, 79, 112, 0.5);
		}

		&:active {
			background-color: rgba(233, 79, 112, 0.4);
		}

		&:first-child {
			margin-bottom: 20px;

			@media (min-width: $screen-md) {
				margin-bottom: 0;
				margin-right: 10px;
			}
		}

		&--deliver {
			color: $color-secondary;

			background-color: rgba(255, 255, 255, 0.4);
			box-shadow: none;

			&:hover,
			&:focus {
				box-shadow: none;
			}
		}
	}
}
