.price-list {
	margin: 0 20px;
	padding: 0;
	list-style: none;

	border-radius: 10px;
	overflow: hidden;

	&__item {
		padding: 20px 12px;

		background: #EDEDED;
		border-bottom: 1px solid rgba(37, 37, 84, 0.1);

		@media (min-width: 500px) {
			display: flex;
			justify-content: space-between;
		}

		&:last-child {
			border-bottom: none;
		}

		&--header {
			background-color: $color-main;
			border-bottom: none;
		}
	}

	&__text {
		display: block;

		font-size: $font-size + 2;
		line-height: 1.5em;
		font-weight: 500;

		&:last-child {
			@media (min-width: $screen-md) {
				max-width: 220px;
				width: 100%;
			}
		}

		&--header {
			color: #ffffff;
		}
	}
}
