.contacts-block-wrapper {
	@media (min-width: $screen-md) {
		display: flex;
		align-items: center;
	}

	&__contacts-block {
		margin-bottom: 20px;

		@media (min-width: $screen-md) {
			margin-bottom: 0;
			margin-right: 56px;
		}

		&:last-child {
			margin-bottom: 0;

			@media (min-width: $screen-md) {
				margin-right: 0;
			}
		}
	}
}
