.info {
	padding: 0 20px;

	@media (min-width: $screen-md) {
		padding: 0 40px;
	}

	@media (min-width: $screen-md + 200) {
		padding: 0;
	}

	&--section1 {
		padding-top: 60px;
		padding-bottom: 44px;

		@media (min-width: $screen-md) {
			position: relative;

			max-width: 1240px;
			width: 100%;
			margin: 0 auto;
			padding-top: 116px;
			padding-bottom: 0;
		}

		&::before,
		&::after {
			@media (min-width: $screen-md + 200) {
				position: absolute;
				z-index: 1;

				content: '';
				display: block;

				background-repeat: no-repeat;
				background-position: center;
				background-size: contain;
			}
		}

		&::before {
			top: -40px;
			left: -130px;

			width: 410px;
			height: 369px;

			background-image: url("../images/index/box1.png");
		}

		&::after {
			top: 30px;
			right: 0;

			width: 245px;
			height: 252px;

			background-image: url("../images/index/box2.png");
		}
	}

	&--section2 {
		position: relative;

		padding-top: 88px;
		padding-bottom: 88px;

		@media (min-width: $screen-md) {
			max-width: 700px;
			width: 100%;
			padding-top: 120px;
			padding-bottom: 120px;
		}
	}

	&--section3 {
		padding-top: 104px;
		padding-bottom: 56px;

		@media (min-width: $screen-md) {
			position: relative;

			max-width: 730px;
			width: 100%;
			margin-left: auto;
			padding-top: 88px;
			padding-bottom: 0;
		}

		&::before {
			@media (min-width: $screen-md + 200) {
				position: absolute;
				top: 40px;
				left: -240px;

				content: '';
				display: block;
				width: 182px;
				height: 164px;

				background-image: url("../images/index/box4.png");
				background-repeat: no-repeat;
				background-position: center;
				background-size: cover;
			}
		}
	}

	&--section4 {
		margin-bottom: 56px;
		padding-top: 72px;
		padding-bottom: 10px;

		@media (min-width: $screen-md) {
			max-width: 1240px;
			width: 100%;
			margin: 0 auto;
			padding-top: 168px;
			padding-bottom: 40px;
		}
	}

	&__title {
		margin: 0;
		margin-bottom: 12px;

		font-size: $font-size-xl;
		line-height: 1.25em;
		font-weight: 800;

		@media (min-width: $screen-md + 200) {
			font-size: 40px;
			line-height: 1em;
		}

		&--centered {
			@media (min-width: $screen-md + 200) {
				margin-bottom: 16px;

				text-align: center;
			}
		}
	}

	&__text {
		margin: 0;
		margin-bottom: 32px;

		font-size: $font-size + 2;
		line-height: 1.5em;
		font-weight: 500;

		@media (min-width: $screen-md + 200) {
			max-width: 620px;
			width: 100%;
			margin-bottom: 80px;

			font-size: $font-size + 2;
			line-height: 1.25em;
		}

		&--centered {
			@media (min-width: $screen-md + 200) {
				max-width: 730px;
				margin: 0 auto;
				margin-bottom: 56px;

				font-size: $font-size-m + 6;
				text-align: center;
			}
		}
	}

	&__pink-title {
		font-weight: 800;
		color: $color-secondary;
	}

	&__pink-text {
		font-weight: 700;
		color: $color-secondary;
	}

	&__info-slider {
		margin-bottom: 80px;
	}

	&__questions-list {
		margin-bottom: 40px;
	}

	&__about-slider {
		margin-bottom: 32px;
	}

	&__form {
		margin-bottom: 32px;

		@media (min-width: $screen-md) {
			margin-bottom: 48px;
		}
	}

	&__about {
		display: block;
		max-width: 200px;
		width: 100%;
		margin: 0 auto;
		margin-bottom: 8px;

		font-size: $font-size-m + 6;
		line-height: 1.25em;
		font-weight: 700;
		color: $color-main;
		text-align: center;

		transition: all 0.3s ease-in-out;

		&:hover {
			color: $color-secondary;
		}
	}

	&__about-text {
		max-width: 180px;
		width: 100%;
		margin: 0 auto;

		font-weight: 500;
		text-align: center;
	}
}
