.language {
	position: absolute;
	top: 32px;
	right: 0;

	display: none;

	margin: 0;
	padding: 0;
	list-style: none;

	@media (min-width: $screen-md) {
		position: static;

		display: flex;
		display: none;
	}

	&__item {
		position: relative;

		display: block;

		margin-bottom: 32px;

		@media (min-width: $screen-md) {
			margin-bottom: 0;
		}

		&:last-child {
			margin-bottom: 0;
		}

		&--active {
			&::before {
				position: absolute;
				bottom: 0;
				left: 10px;
				z-index: 0;

				content: '';
				display: block;
				width: 30px;
				height: 100px;

				background-color: #fff;
				transform: skewX(-20deg);
			}
		}
	}

	&__link {
		position: relative;
		z-index: 1;

		display: block;

		padding: 8px;

		font-size: $font-size-xs;
		font-weight: 600;
		text-transform: uppercase;

		transition: color 0.3s ease-in-out;

		@media (min-width: $screen-md) {
			padding-bottom: 4px;
		}

		&:hover {
			color: $color-secondary;
		}
	}
}
