.bg-clip {
	&--section1 {
		position: relative;

		background-color: #EDEDED;
		clip-path: polygon(0 0,100% 3%,100% 97%,0 100%);

		@media (min-width: $screen-md) {
			clip-path: polygon(0 0, 100% 14%, 100% 86%, 0% 100%);
		}

		&::before {
			position: absolute;
			top: 0;
			right: 0;

			content: '';
			display: block;
			width: 100%;
			height: 100%;

			background-image: url("../images/about/section1-bg-mobile@1x.png");
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;

			@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
				background-image: url("../images/about/section1-bg-mobile@2x.png");
			}

			@media (min-width: $screen-md + 200) {
				background-image: url("../images/about/section1-bg-desktop@1x.png");
				background-position: 300px center;
				background-size: 1347px auto;

				@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
					background-image: url("../images/about/section1-bg-desktop@2x.png");
				}
			}

			@media (min-width: $screen-lg) {
				background-position: center right;
			}
		}
	}

	&--section3 {
		position: relative;

		background: rgb(237,237,237);
		background: linear-gradient(180deg, rgba(237,237,237,1) 0%,
																				rgba(237,237,237,1) 50%,
																				rgba(255,255,255,1) 80%,
																				rgba(255,255,255,1) 100%);

		clip-path: polygon(0 0, 100% 4%, 100% 100%, 0% 100%);

		@media (min-width: $screen-md) {
			clip-path: polygon(0 0, 100% 15%, 100% 100%, 0% 100%);
		}

		&::before {
			position: absolute;
			bottom: 0;
			left: 0;
			z-index: 1;

			content: '';
			display: block;
			width: 100%;
			height: 100%;

			background-image: url("../images/about/section3-bg-mobile@1x.png");
			background-repeat: no-repeat;
			background-position: left top;
			background-size: cover;

			@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
				background-image: url("../images/about/section3-bg-mobile@2x.png");
			}

			@media (min-width: $screen-md) {
				background-image: url("../images/about/section3-bg-desktop@1x.png");

				@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
					background-image: url("../images/about/section3-bg-desktop@2x.png");
				}
			}
		}
	}
}
