.faq-list {
	margin: 0;
	padding: 0;
	list-style: none;

	border-top: 1px solid rgba(37, 37, 84, 0.1);

	&__item {
		border-bottom: 1px solid rgba(37, 37, 84, 0.1);

		&.active {
			padding-bottom: 24px;
		}

		&:last-child {
			border-bottom: none;
		}
	}

	&__question {
		position: relative;

		display: block;
		width: 100%;

		margin: 0;
		padding: 25px 20px;
		padding-right: 40px;

		font: inherit;
		font-size: $font-size + 2;
		line-height: 1.25em;
		font-weight: 700;
		color: $color-main;
		text-align: left;

		border: none;
		background-color: transparent;
		outline: none;
		cursor: pointer;

		&::before {
			position: absolute;
			top: 20px;
			right: 20px;
			z-index: 0;

			content: '';
			display: block;
			width: 30px;
			height: 30px;

			background-image: url("../images/icons/plus.svg");
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
		}

		&.active {
			&::before {
				background-image: url("../images/icons/minus.svg");
			}
		}
	}

	&__answer {
		margin: 0;
		padding: 0 20px;

		display: none;

		font-size: $font-size + 2;
		line-height: 1.5em;
		font-weight: 500;
	}

	&__link {
		color: #3792DF;

		transition: color 0.3s ease-in-out;

		&:hover {
			color: $color-secondary;
		}
	}
}
