.page-footer {
	padding: 20px;
	padding-top: 40px;

	background: rgba(216, 216, 216, 0.0001);

	@media (min-width: $screen-md) {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	@media (min-width: $screen-md + 200) {
		max-width: 1360px;
		width: 100%;
		margin: 0 auto;
		padding: 32px 60px;
		padding-top: 48px;
	}

	&--about {
		position: relative;
		z-index: 1;
	}

	&__copyright {
		display: block;

		font-size: $font-size-s;
		line-height: 1.25em;
		font-weight: 500;
		text-align: center;

		opacity: 0.4;
	}
}
