.about-intro {
	padding: 0 20px;

	overflow: hidden;

	@media (min-width: $screen-md + 200) {
		max-width: 530px;
		width: 100%;
		padding: 0;

		overflow: initial;
	}

	&__title {
		max-width: 220px;
		width: 100%;
		margin: 0;
		margin-bottom: 20px;

		font-size: $font-size-xl;
		line-height: 1.34em;
		font-weight: 900;
		letter-spacing: 1px;
		text-transform: uppercase;

		@media (min-width: 400px) {
			max-width: none;
		}

		@media (min-width: $screen-md + 200) {
			font-size: 43px;
			line-height: 60px;
			letter-spacing: 1.72px ;
		}
	}

	&__pink-title {
		color: $color-secondary;
	}

	&__intro-text {
		margin: 0;
		margin-bottom: 20px;

		font-size: $font-size-m;
		line-height: 1.67em;
		font-weight: 700;

		@media (min-width: $screen-md + 200) {
			margin-bottom: 32px;
		}
	}

	&__button {
		display: block;

		max-width: 330px;
		width: 100%;
		margin: 0 auto;
		margin-bottom: 32px;
		padding: 16px;

		font: inherit;
		font-weight: 700;
		line-height: 1.3em;
		color: #ffffff;
		text-align: center;

		border: none;
		background-color: $color-main;
		border-radius: 25px;
		box-shadow: 0 10px 30px 0 rgba(37, 37, 84, 0.5);
		cursor: pointer;
		outline: none;

		transition: all 0.3s ease-out;

		@media (min-width: $screen-md + 200) {
			max-width: 210px;
			margin: 0;
			margin-bottom: 20px;
		}

		&:hover,
		&:focus {
			color: #ffffff;

			background-color: $color-secondary;
			box-shadow: 0 10px 30px 0 rgba(233, 79, 112, 0.5);
		}

		&:active {
			background-color: rgba(233, 79, 112, 0.4);
		}
	}

	&__inner-wrapper {
		@media (min-width: $screen-md + 200) {
			display: grid;
			grid-template-columns: 330px 1fr;
			grid-gap: 20px;
			grid-template-areas:
				"text slide3"
				"slide1 slide2";
		}
		justify-items: end;
	}

	&__text {
		margin: 0;
		margin-bottom: 32px;

		font-weight: 500;
		line-height: 1.78em;

		@media (min-width: $screen-md + 200) {
			grid-area: text;

			margin-bottom: 0;
		}
	}
}
