.field-text {
	position: relative;

	&--date {
		&::before {
			position: absolute;
			top: 50%;
			left: calc(50% - 90px);
			transform: translateY(-50%);
			z-index: -1;

			content: '';
			display: block;
			width: 30px;
			height: 30px;

			background-image: url("../images/icons/calendar.svg");
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
		}
	}

	&--parcel {
		display: none;

		margin-top: 24px;
	}

	&--bordered {
		display: none;

		max-width: 330px;
		margin: 0 auto;
		margin-bottom: 20px;
	}

	&--phone {
		display: block;
	}

	&__input {
		width: 100%;
		padding: 20px;

		font: inherit;
		font-weight: 700;
		line-height: 20px;
		color: $color-main;
		text-align: center;

		border: none;
		background-color: transparent;
		cursor: pointer;

		outline: none;

		&::placeholder {
			color: $color-main;

			transition: all 0.3s ease-in-out;
		}

		&:hover {
			&::placeholder {
				color: $color-secondary;
			}
		}

		&--bordered {
			padding: 13px 20px;

			border: 2px solid #252554;
			box-shadow: 0 5px 10px rgba(37, 37, 84, 0.2);
			border-radius: 25px;
		}
	}

	&__textarea {
		display: block;
		max-width: 330px;
		width: 100%;
		margin: 0 auto;

		min-height: 120px;
		padding: 16px 20px;

		font-weight: 500;
		text-align: left;

		border: 2px solid #252554;
		box-sizing: border-box;
		box-shadow: 0 5px 10px rgba(37, 37, 84, 0.2);
		border-radius: 25px;

		resize: none;
	}
}
