.about-section {
	position: relative;

	&--section1 {
		&::after {
			position: absolute;
			top: -40px;
			right: 0;

			content: '';
			display: block;
			width: 176px;
			height: 196px;

			background-image: url("../images/about/section1-box-mobile@1x.png");
			background-repeat: no-repeat;
			background-position: center;
			background-size: contain;

			@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
				background-image: url("../images/about/section1-box-mobile@2x.png");
			}

			@media (min-width: $screen-md + 200) {
				display: none;
			}
		}
	}

	&--section2 {
		&::before {
			position: absolute;
			top: -100px;
			right: 0;

			content: '';
			display: block;
			width: 260px;
			height: 259px;

			background-image: url("../images/about/section2-bg-mobile@1x.png");
			background-repeat: no-repeat;
			background-position: center;
			background-size: contain;

			@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
				background-image: url("../images/about/section2-bg-mobile@2x.png");
			}

			@media (min-width: $screen-md + 200) {
				display: none;
			}
		}
	}
}
