.info-bg-clip {
	&--section2 {
		position: relative;

		background-color: #F2F2F2;
		clip-path: polygon(0 0,100% 3%,100% 97%,0 100%);

		@media (min-width: $screen-md) {
			clip-path: polygon(0 0, 100% 14%, 100% 86%, 0% 100%);
		}

		&::before {
			position: absolute;
			top: 0;
			right: 0;

			content: '';
			display: block;
			width: 100%;
			height: 100%;

			background-image: url("../images/index/info2-bg-mobile@1x.png");
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;

			@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
				background-image: url("../images/index/info2-bg-mobile@2x.png");
			}

			@media (min-width: $screen-md + 200) {
				background-image: url("../images/index/info2-bg-desktop@1x.png");
				background-position: right center;
				background-size: 100%;

				@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
					background-image: url("../images/index/info2-bg-desktop@2x.png");
				}
			}
		}
	}

	&--section4 {
		background: linear-gradient(180deg,#E2E2E2 0,
																			 #E2E2E2 50%,
																			 #FFFFFF 80%,
																			 #FFFFFF 100%);

		clip-path: polygon(0 0,100% 4%,100% 100%,0 100%);

		@media (min-width: $screen-md) {
			clip-path: polygon(0 0, 100% 15%, 100% 100%, 0% 100%);
		}
	}
}
