.how-works {
	position: absolute;
	bottom: 0;
	right: 0;

	display: flex;
	align-items: center;

	width: 222px;
	height: 385px;

	background-image: url("../images/index/how-works-bg.png");
	background-repeat: no-repeat;
	background-position: right center;
	background-size: cover;

	@media (min-width: $screen-md) {
		bottom: auto;
		top: 0;
	}

	@media (min-width: $screen-lg) {
		width: 323px;
		height: 557px;
	}

	&__button {
		display: flex;
		align-items: center;

		max-width: 185px;
		width: 100%;
		margin: 0;
		padding: 3px 0;
		padding-left: 20px;

		font: inherit;
		text-align: left;

		border: 2px solid #252554;
		border-radius: 54px;
		background-color: transparent;
		cursor: pointer;
		outline: none;

		@media (min-width: $screen-md) {
			position: relative;
			right: 104px;

			margin-top: 170px;
		}

		@media (min-width: $screen-lg) {
			right: 80px;
			margin-top: 250px;
		}
	}

	&__text {
		max-width: 100px;

		font-size: $font-size;
		line-height: 1.43em;
		font-weight: 700;
		color: $color-main;
	}

	&__icon {
		display: block;

		width: 58px;

		object-fit: contain;
	}
}
