.info-links {
	display: flex;
	flex-wrap: wrap;

	max-width: 290px;
	width: 100%;
	margin: 0 auto;
	padding: 0;
	list-style: none;

	@media (min-width: $screen-md) {
		max-width: none;
		margin: 0;
	}

	@media (min-width: $screen-lg) {
		max-width: 480px;
	}

	&__item {
		margin-right: 16px;
	}

	&__link {
		display: block;

		padding: 4px 0;

		font-size: $font-size-s;
		line-height: 2.5em;
		color: $color-main;

		transition: all 0.3s ease-in-out;

		&:hover,
		&:focus {
			color: $color-secondary;
		}
	}
}
