.about-wrapper {
	max-width: 1240px;
	width: 100%;
	margin: 0 auto;

	@media (min-width: $screen-md + 200) {
		position: relative;
	}

	&::before {
		@media (min-width: $screen-md + 200) {
			position: absolute;
			bottom: 0;
			left: 0;

			content: '';
			display: block;
			width: 175px;
			height: 141px;

			background-image: url("../images/about/page3.png");
			background-repeat: no-repeat;
			background-position: center;
			background-size: contain;
		}
	}

	&::after {
		@media (min-width: $screen-md + 200) {
			position: absolute;
			top: -150px;
			left: 30px;

			content: '';
			display: block;
			width: 781px;
			height: 962px;

			background-image: url("../images/about/section2-bg-desktop@1x.png");
			background-repeat: no-repeat;
			background-position: center;
			background-size: contain;

			@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
				background-image: url("../images/about/section2-bg-desktop@2x.png");
			}
		}
	}
}
