.how-works-slider {
	display: block;

	margin: 0 -20px;
	padding: 0;
	list-style: none;

	@media (min-width: $screen-md) {
		margin: 0;
	}

	&__item {
		display: flex;
		align-items: center;

		max-width: 230px;
		width: 100%;
		min-height: 100px;
		padding-left: 20px;

		@media (min-width: $screen-md) {
			padding-left: 0;
			margin-right: 20px;
		}

		&:last-child {
			padding-right: 20px;

			@media (min-width: $screen-md) {
				padding-right: 0;
				margin-right: 0;
			}
		}
	}

	&__icon {
		display: block;
		flex-shrink: 0;

		width: 60px;
		height: 64px;
		margin-top: 8px;
		margin-right: 10px;

		object-fit: contain;
	}

	&__text {
		margin: 0;

		font-size: $font-size-s;
		line-height: 1.2em;
		font-weight: 500;
	}
}
