.questions-list {
	margin: 0;
	padding: 0;
	list-style: none;

	@media (min-width: $screen-md) {
		display: flex;
	}

	&__item {
		margin-bottom: 16px;

		@media (min-width: $screen-md) {
			margin-bottom: 0;
			margin-right: 24px;
		}

		&:last-child {
			margin-bottom: 0;

			@media (min-width: $screen-md) {
				margin-right: 0;
			}
		}
	}

	&__link {
		display: block;
		padding: 8px 0;

		font-weight: 500;
		color: #3792DF;
		text-align: center;

		transition: all 0.3s ease-in-out;

		&:hover,
		&:focus {
			color: $color-secondary;
		}
	}
}
