.form-field-text {
	position: relative;

	border-bottom: 1px solid rgba(37, 37, 84, 0.1);

	@media (min-width: $screen-md) {
		flex-shrink: 0;

		max-width: 180px;
		width: 100%;

		border-bottom: none;
		border-right: 1px solid rgba(37, 37, 84, 0.1);
	}

	&--date {
		&::before {
			position: absolute;
			top: 50%;
			right: 70%;

			transform: translateY(-50%);
			z-index: -1;

			content: '';
			display: block;
			width: 30px;
			height: 30px;

			background-image: url("../images/icons/calendar.svg");
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;

			@media (min-width: $screen-md) {
				left: 20px;
			}
		}
	}

	&__input {
		width: 100%;
		padding: 25px;

		font: inherit;
		font-weight: 700;
		line-height: 20px;
		color: $color-main;
		text-align: center;

		border: none;
		background-color: transparent;
		cursor: pointer;

		outline: none;

		@media (min-width: $screen-md) {
			padding: 25px 20px;
			padding-left: 50px;
		}

		&::placeholder {
			color: $color-main;

			transition: color 0.3s ease-in-out;
		}

		&:hover {
			&::placeholder {
				color: $color-secondary;
			}
		}
	}
}
