.alert-message {
	position: relative;

	padding: 16px 24px;

	background: #45C167;
	box-shadow: 0 10px 40px rgba(69, 193, 103, 0.5);

	&--error {
		background: #E94F70;
		box-shadow: 0 10px 40px rgba(233, 79, 112, 0.5);
	}

	&__close {
		right: 20px;
		top: 50%;
		transform: translateY(-50%);
	}

	&__text {
		margin: 0;

		font-weight: 500;
		color: #ffffff;
	}
}
