.to-top {
	max-width: 1360px;
	width: 100%;
	margin: 0 auto;
	padding: 0 20px;

	@media (min-width: $screen-md + 200) {
		padding: 0 60px;
	}

	&__button {
		display: flex;
		align-items: center;
		justify-content: center;

		width: 60px;
		height: 60px;
		margin: 0 auto;
		padding: 0;

		border: none;
		box-shadow: 0 10px 20px rgba(37, 37, 84, 0.3);
		border-radius: 50%;
		background-color: $color-main;
		cursor: pointer;
		outline: none;

		@media (min-width: $screen-md) {
			margin-right: 0;
		}
	}

	&__icon {
		display: block;

		width: 30px;

		object-fit: contain;
	}
}
