.form-field-select-wrapper {
	position: relative;

	width: 100%;

	&::before {
		position: absolute;
		top: 50%;
		right: 10px;
		transform: translateY(-50%);
		z-index: -1;

		content: '';
		display: block;
		width: 30px;
		height: 30px;

		background-image: url("../images/icons/select-arrow.svg");
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
	}
}
