.socials {
	position: absolute;
	bottom: 90px;
	left: 30px;

	display: flex;

	margin: 0;
	padding: 0;
	list-style: none;

	transform: rotate(-90deg);
	transform-origin: bottom left;

	@media (min-width: $screen-md) {
		bottom: 200px;
	}

	@media (min-width: $screen-lg) {
		bottom: 340px;
	}

	&__item {
		&:first-child {
			margin-right: 24px;
		}
	}

	&__link {
		display: block;
		padding: 4px;

		font-size: $font-size-xs;
		line-height: 1.2em;
		font-weight: 700;
		letter-spacing: 0.1em;
		color: $color-main;
		text-transform: uppercase;

		transition: all 0.3s ease-in-out;

		&:hover,
		&:focus {
			color: $color-secondary;
		}
	}
}
