.social-widgets {
	@media (min-width: $screen-md) {
		display: flex;

		width: 100%;
	}

	&__insta-mobile {
		max-width: 330px;
		width: 100%;
		margin: 0 auto;

		background: #D8D8D8;
		box-shadow: 0 10px 50px rgba(0, 0, 0, 0.2);
		border-radius: 15px;

		@media (min-width: $screen-md) {
			display: none;
		}

		div {
			border-radius: 15px;
		}
	}

	&__insta-desktop {
		display: none;

		@media (min-width: $screen-md) {
			display: block;

			max-width: 270px;
			width: 100%;

			background: #D8D8D8;
			box-shadow: 0 10px 50px rgba(0, 0, 0, 0.2);
			border-radius: 15px;

			div {
				border-radius: 15px;
			}
		}
	}

	&__fb-desktop {
		display: none;

		@media (min-width: $screen-md) {
			display: block;

			max-width: 600px;
			width: 100%;
			max-height: 600px;
			margin-right: 40px;

			overflow: hidden;
			background: #D8D8D8;
			box-shadow: 0 10px 50px rgba(0, 0, 0, 0.2);
			border-radius: 15px;

			div {
				border-radius: 15px;
			}
		}
	}
}
