.about {
	padding: 0 20px;

	&--section1 {
		position: relative;

		padding-top: 48px;
		padding-bottom: 48px;

		@media (min-width: $screen-md) {
			max-width: 600px;
			width: 100%;
			padding-top: 136px;
			padding-bottom: 168px;
		}

		@media (min-width: $screen-md + 200) {
			max-width: 1240px;
			margin: 0 auto;
			padding-left: 0;
			padding-right: 0;
		}

		&::before {
			@media (min-width: $screen-md + 200) {
				position: absolute;
				bottom: 100px;
				right: 0;

				content: '';
				display: block;
				width: 175px;
				height: 141px;

				background-image: url("../images/about/page2.png");
				background-repeat: no-repeat;
				background-position: center;
				background-size: contain;
			}
		}
	}

	&--section2 {
		padding-top: 104px;
		padding-bottom: 40px;

		@media (min-width: $screen-md) {
			max-width: 640px;
			width: 100%;
			margin-left: auto;
			padding-top: 56px;
			padding-bottom: 88px;
		}

		@media (min-width: $screen-md + 200) {
			position: relative;
			z-index: 1;

			padding-left: 0;
			padding-right: 0;
		}
	}

	&--section3 {
		position: relative;
		z-index: 1;

		padding-top: 56px;
		padding-bottom: 32px;

		@media (min-width: $screen-md) {
			padding-top: 134px;
			padding-bottom: 80px;
		}

		@media (min-width: $screen-md + 200) {
			max-width: 1240px;
			margin: 0 auto;
			padding-left: 0;
			padding-right: 0;
		}

		&::before {
			@media (min-width: $screen-md + 200) {
				position: absolute;
				bottom: 0;
				right: 0;

				content: '';
				display: block;
				width: 185px;
				height: 141px;

				background-image: url("../images/about/page4.png");
				background-repeat: no-repeat;
				background-position: center;
				background-size: contain;
			}
		}
	}

	&__title {
		margin: 0;
		margin-bottom: 16px;

		font-size: $font-size-xl;
		line-height: 1.34em;
		font-weight: 800;

		@media (min-width: $screen-md) {
			margin-bottom: 32px;

			font-size: 80px;
			line-height: 90px;
		}

		&--section1 {
			max-width: 300px;
			width: 100%;

			@media (min-width: $screen-md + 200) {
				max-width: 560px;
			}
		}

		&--section3 {
			@media (min-width: $screen-md) {
				max-width: 620px;
				width: 100%;
				margin-bottom: 56px;
			}
		}
	}

	&__pink-title {
		color: $color-secondary;
	}

	&__text {
		margin: 0;
		margin-bottom: 32px;

		font-weight: 500;
		line-height: 1.7em;

		&--section1 {
			@media (min-width: $screen-md) {
				max-width: 460px;
				width: 100%;
			}
		}

		&--section2 {
			@media (min-width: $screen-md) {
				max-width: 280px;
				width: 100%;
			}
		}
	}

	&__pink-text {
		color: $color-secondary;
	}

	&__bold-text {
		font-weight: 700;
	}

	&__how-works-slider {
		margin-bottom: 48px;

		@media (min-width: $screen-md) {
			margin-bottom: 80px;
		}
	}
}
