.field-radio {
	display: block;
	width: 100%;
	margin: 0;

	cursor: pointer;

	&__input {
		display: none;
	}

	&__text {
		position: relative;

		display: block;

		padding: 14px 0;
		padding-left: 20px;

		line-height: 1.3em;
		font-weight: 500;
		color: $color-main;
		text-align: center;

		border: 2px solid #252554;

		&::before {
			position: absolute;
			top: 50%;
			left: 20px;
			left: calc(50% - 60px);
			transform: translateY(-50%);

			content: '';
			display: block;
			height: 30px;

			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
		}

		&--doc {
			border-top-left-radius: 25px;
			border-bottom-left-radius: 25px;
			border-right: none;

			&::before {
				width: 24px;

				background-image: url("../images/icons/documents.svg");
			}
		}

		&--parcel {
			border-top-right-radius: 25px;
			border-bottom-right-radius: 25px;
			border-left: none;

			&::before {
				width: 29px;

				background-image: url("../images/icons/parcel.svg");
			}
		}
	}

	&__warning-text {
		margin: 0;
		padding: 0 20px;

		font-size: $font-size-s;
		line-height: 1.2em;
		font-weight: 500;
		text-align: center;

		opacity: 0.4;
	}
}

.field-radio__input:checked + .field-radio__text {
	color: #ffffff;

	background-color: $color-main;
}
