.subscribe-info {
	text-align: center;

	@media (min-width: $screen-md) {
		text-align: left;
	}

	&__text {
		margin: 0;
		font-weight: 500;

		&:last-of-type {
			margin-bottom: 12px;
		}

		&--bold {
			font-weight: 700;
		}
	}

	&__link {
		display: block;

		max-width: 280px;
		width: 100%;
		margin: 0 auto;
		padding: 8px 0;

		color: $color-secondary;
		text-decoration: underline;
		text-decoration-style: dashed;

		transition: color 0.3s ease-in-out;

		@media (min-width: $screen-md) {
			margin: 0;

			text-align: left;
		}

		&:hover {
			color: $color-main;
			text-decoration: underline;
			text-decoration-style: dashed;
		}
	}
}
