html {
	height: 100%;

	box-sizing: border-box;
	scroll-behavior: smooth;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

body {
	position: relative;

	min-width: 320px;
	height: 100%;
	margin: 0;
	padding: 0;

	color: $color-main;
	font-family: $font-family;
	font-size: $font-size;
	line-height: $line-height;
	font-weight: 400;

	overflow-x: hidden;
}

img {
	max-width: 100%;
	height: auto;
}

a {
	text-decoration: none;

	&:hover,
	&:focus {
		text-decoration: none;
	}
}
