.about-slider {
	display: block;

	margin: 0 -20px;
	padding: 0;
	list-style: none;

	@media (min-width: 710px) {
		margin: 0;
	}

	&__item {
		display: flex;
		align-items: center;

		max-width: 240px;
		width: 100%;
		min-height: 100px;
		padding-left: 20px;

		@media (min-width: 710px) {
			padding-left: 0;
			margin-right: 20px;
		}

		&:last-child {
			max-width: 260px;
			padding-right: 20px;

			@media (min-width: 710px) {
				max-width: 240px;
				margin-right: 0;
				padding-right: 0;
			}
		}
	}

	&__img {
		display: block;
		align-self: flex-end;
		flex-shrink: 0;

		margin-right: 10px;

		object-fit: cover;
	}

	&__text {
		margin: 0;

		font-size: $font-size-s;
		line-height: 1.2em;
		font-weight: 500;
	}
}
