.about-page {
	max-width: 980px;
	width: 100%;
	margin: 0 auto;
	padding: 0 20px;

	@media (min-width: $screen-md) {
		padding: 0;
	}

	&__intro-title {
		margin: 0;
		margin-bottom: 20px;

		font-size: $font-size-xl;
		line-height: 1.34em;

		@media (min-width: $screen-md) {
			margin-bottom: 24px;
			padding: 0 80px;

			font-size: 46px;
			line-height: 1.3em;
		}
	}

	&__intro {
		margin: 0;
		margin-bottom: 20px;

		font-size: $font-size + 2;
		line-height: 1.5em;
		font-weight: 500;

		@media (min-width: $screen-md) {
			margin-bottom: 56px;
			padding: 0 80px;
		}
	}

	&__title {
		margin: 0;
		margin-bottom: 24px;

		font-size: $font-size-m;
		line-height: 1.39em;

		@media (min-width: $screen-md) {
			padding: 0 80px;

			font-size: $font-size-xl - 4;
			line-height: 1.25em;
		}
	}

	&__text {
		margin: 0;
		margin-bottom: 40px;

		font-size: $font-size + 2;
		line-height: 1.5em;
		font-weight: 500;

		@media (min-width: $screen-md) {
			padding: 0 80px;
		}

		&--no-mb {
			margin-bottom: 0;
		}
	}
}
