.form-field-select {
	width: 100%;
	padding: 25px;

	font: inherit;
	line-height: 20px;
	font-weight: 700;
	color: $color-main;

	border: none;
	background-color: transparent;
	border-radius: 10px;
	cursor: pointer;

	-webkit-appearance: none;
	-moz-appearance: none;
	outline: none;

	transition: all 0.3s ease-in-out;

	&::-ms-expand {
		display: none;
	}

	&:hover {
		color: $color-secondary;
	}

	option {
		color: $color-main;

		&:disabled {
			color: #c1c1c1;
		}

		&:first-child {
			display: none;
		}
	}

	&__empty {
		display: none;
	}
}
