.sub-form {
	@media (min-width: $screen-md) {
		max-width: 310px;
		width: 100%;
	}

	&__title {
		margin: 0;
		margin-bottom: 24px;

		font-size: $font-size-xl;
		line-height: 1.34em;
		font-weight: 800;
	}

	&__text {
		margin: 0;
		margin-bottom: 32px;

		font-size: $font-size + 2;
		line-height: 1.5em;
		font-weight: 500;
	}

	&__field-text {
		margin-bottom: 20px;

		border: 2px solid #252554;
		box-shadow: 0 5px 10px rgba(37, 37, 84, 0.2);
		border-radius: 25px;
	}

	&__input {
		padding: 15px;
	}

	&__button {
		margin-bottom: 24px;

		line-height: 1.3em;
		text-transform: none;

		box-shadow: 0 10px 30px 0 rgba(37, 37, 84, 0.5);

		transition: all 0.3s ease-in-out;

		&:hover,
		&:focus {
			background-color: $color-secondary;
			box-shadow: 0 10px 30px 0 rgba(233, 79, 112, 0.5);
		}

		&:active {
			background-color: rgba(233, 79, 112, 0.4);
		}
	}

	&__info-text {
		margin: 0;

		font-size: $font-size-s;
		line-height: 1.2em;
		font-weight: 500;

		opacity: 0.4;
	}
}
